body,
html,
.app {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

* {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background: #000000;
  color: white;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
a {
  margin: 0;
  padding: 0;
}

img {
  width: 60px;
}

.intro {
  height: 80vh;
  position: relative;
  background: url('https://cdn-images-1.medium.com/max/2400/1*fgl8pzsmUqAbb_PVWJvAng.jpeg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: saturation;
  color: white;
  box-shadow: inset 0 0 1000px black;
}

.navbar li,
li a {
  position: relative;
  color: white;
  text-decoration: none;
  list-style: none;
  font-size: 1.6rem;
}

.navbar ul {
  display: flex;
  width: 65%;
  margin: auto;
  justify-content: space-evenly;
  border-bottom: 1px white solid;
  padding-bottom: 30px;
  padding-top: 30px;
}

.navbar {
  width: 100%;
}

.introductionText {
  text-align: center;
  padding-top: 100px;
}

.introductionText h1 {
  text-shadow: 0 0 50px black;
  font-size: 3rem;
}

.introductionText p {
  text-shadow: 0 0 5px black;
  font-size: 1.42rem;
}

.CTA {
  background: #c53fc5;
  display: flex;
  align-items: center;
  color: white;
  justify-content: space-evenly;
}

.icons>img {
  margin-right: 10px;
}

.about {
  display: flex;
  background: black url('bg.jpg');

  height: 100vh;
  color: white;
  flex-direction: row-reverse;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left top;
}

.about>div {
  width: 50%;
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background: #3b3b3b6c;
  padding: 0 100px;
  box-sizing: border-box;
}

.about>div>hr {
  width: 75%;
}

button {
  font-size: 20px;
  cursor: pointer;
  background: white;
  color: black;
  border: 1px solid white;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  padding: 10px 15px;
  line-height: 1;
  margin: 0 auto;
}

button:focus {
  outline: none;

}

.footer {
  background: #58119e;
  padding: 50px 100px;
  text-align: center;
}

.footer h1 {
  font-weight: 100;
  opacity: .6;
  font-size: 17px;
}

.projects {
  background: linear-gradient(to bottom, #7f7f7f, #000000);
  color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 100px;

}

.projects hr {
  width: 550px;
}

.producing {
  text-align: center;
  opacity: 0.5;
}

.producing h1 {
  font-weight: 100;
  font-size: 3rem;
}

.cards {
  margin-top: 120px;
  padding: 0 10%;

  display: flex;
}

.cards * {
  font-weight: 100;
}

.card {
  padding: 0 100px;
}

.reviews {
  display: flex;

  background: #a85ff0 url('https://cdn2.hercampus.com/hero-images/2019/01/09/matt-botsford-197870-unsplash.jpg');
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;
  flex-direction: row-reverse;
}

.reviews div {
  width: 50%;
  text-align: left;
  padding: 50px 100px;
  font-size: 1.4rem;
  font-weight: 100;
}

.reviews div h3 {
  font-weight: 400;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 250px;
  grid-gap: 10px;
  margin-bottom: 100px;
}

.project {
  position: relative;

}

.front img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.front,
.back {
  width: 100%;
  height: 100%;
  overflow: hidden;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  position: absolute;
  transition: transform .6s linear;
  -webkit-transition: transform .6s linear;
  -moz-transition: transform .6s linear;
  -ms-transition: transform .6s linear;
  -o-transition: transform .6s linear;
}

.front {
  transform: perspective(1200px) rotateY(0);
  -webkit-transform: perspective(1200px) rotateY(0);
  -moz-transform: perspective(1200px) rotateY(0);
  -ms-transform: perspective(1200px) rotateY(0);
  -o-transform: perspective(1200px) rotateY(0);
}

.back {
  background: rgb(170, 170, 170);
  transform: perspective(1200px) rotateY(180deg);
  -webkit-transform: perspective(1200px) rotateY(180deg);
  -moz-transform: perspective(1200px) rotateY(180deg);
  -ms-transform: perspective(1200px) rotateY(180deg);
  -o-transform: perspective(1200px) rotateY(180deg);
}

.back-content {
  
  box-sizing: border-box;
  padding: 10px 50px;
  color: #ffffff;
  text-align: center;
  width: 100%;
}

.project {
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.project:hover>.front {
  transform: perspective(1200px) rotateY(-180deg);
  -webkit-transform: perspective(1200px) rotateY(-180deg);
  -moz-transform: perspective(1200px) rotateY(-180deg);
  -ms-transform: perspective(1200px) rotateY(-180deg);
  -o-transform: perspective(1200px) rotateY(-180deg);
}

.project:hover>.back {
  transform: perspective(1200px) rotateY(0deg);
  -webkit-transform: perspective(1200px) rotateY(0deg);
  -moz-transform: perspective(1200px) rotateY(0deg);
  -ms-transform: perspective(1200px) rotateY(0deg);
  -o-transform: perspective(1200px) rotateY(0deg);
}

.projects-grid div:nth-child(2) {
  grid-column: 3;
  grid-row: 2 / 4;
}

.projects-grid div:nth-child(5) {
  grid-column: 1 / 3;
  grid-row: 1 / 3;
}

.portfolio {
  background: gray;
  padding: 100px 8%;
}

.portfoliocontainer {
  background: rgb(98, 98, 98);
  border: black solid 1px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  padding: 20px 150px;
}

.portfoliocontainer>p {
  opacity: .7;
  font-size: 20px;
  font-weight: 100;
  padding: 10px 50px;
}

.portfoliocontainer hr {
  width: 250px;
}

.timeline {
  /* border-top:1px white solid; */
  position: relative;
  display: flex;
  justify-content: space-around;
  margin: 0 122px;
  margin-top: 150px;
  border-top: white 1px solid;
  text-align: center;

}

.timestamp {
  text-align: center;
  position: relative;
}

.timestamp p {
  padding: 0 !important;
  margin: 0;
  top: -12px;
  position: relative;
  font-weight: 100;
}

.circle {
  position: relative;
  top: -15px;
  text-align: center;
  content: "";
  border: white 2px solid;
  width: 25px;
  height: 25px;
  margin: 0 auto;
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
  -o-border-radius: 100%;

}

.clear {
  border: white 1px solid;
  color: white;
  background: none;
  transition: .2s ease-in-out;
  -webkit-transition: .2s ease-in-out;
  -moz-transition: .2s ease-in-out;
  -ms-transition: .2s ease-in-out;
  -o-transition: .2s ease-in-out;
}


button:hover {
  background: white;
  color: #000000;
  padding: 10px 28px;
  transition: all .2s ease-in-out;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
}

.portfoliocontainer h1,
.projects h1,
.about h1 {
  font-size: 40pt;
  font-weight: 100;
}
.about p{
  text-align: left;
  opacity: .7;
  padding-bottom: 50px;
  font-weight: 100;
  font-size: 22px;
}